.alert {
    padding:10px;
    border-width: 4px;
    border-style: solid;
    border-radius: 5px;
    background-color: lightgrey;
    font-size: 28px;    
    margin-bottom: 10px;
}

.success {
    color:green;
    border-color: green;
}

.error {
    color:red;
    border-color: red;
}